import Logo from '../images/logo.png';
import  "../App.css";
// import history from '../classNamees/history';
import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import  '../css/navbar.css';
import MobIcon from '../images/image.png'; 



const Header = ({ history }) => (

    <div className="Header">
        <div className="topheader">
<ul>
    <li><div >Store</div></li>
    <li><div>Shop</div></li>
    <li><div>Policy</div></li>
    </ul>

        </div>

          <div>
          <nav>
        <div id="logo"><img src={Logo} className="App-logo" alt="logo" /></div>

        <label htmlFor="drop" className="toggle"><img src={MobIcon} /></label>
        
        <input type="checkbox" id="drop" />
            <ul className="menu">
                <li>
                   <div onClick={()=> history.push('/deploy/')}>Home</div>
                 </li>
                <li>
                    <label htmlFor="drop-1" className="toggle">Bikes +</label>
                   <div>Bikes +</div>
                    <input type="checkbox" id="drop-1"/>
                    <ul className="bikedropcss">
                        <li><div onClick={()=> history.push('/deploy/cr70/')} >70cc</div></li>
                        <li><div onClick={()=> history.push('/deploy/cr100/')} >100cc</div></li>
                        <li><div onClick={()=> history.push('/deploy/cr125/')} >125cc</div></li>
                        <li><div onClick={()=> history.push('/deploy/fighter/')} >Fighter</div></li>
                    </ul> 

                </li>
                <li>

                
                <label htmlFor="drop-2" className="toggle">Loaders </label>
                 <div onClick={()=> history.push('/deploy/loader/bike/')} >Loaders </div>
                <input type="checkbox" id="drop-2"/>
                {/* <ul>
                    <li><div onClick={()=> history.push('/deploy/loader/bike/')} >Loader 100cc</div></li>
                    <li><div onClick={()=> history.push('/deploy/bike/')} >Loader 125cc</div></li>
                    <li><div onClick={()=> history.push('/deploy/bike/')} >Loader 150cc</div></li>
                    <li><div onClick={()=> history.push('/deploy/bike/')} >Loader 200cc</div></li>
</ul> */}

                </li>
                <li>
                   <div onClick={()=> history.push('/deploy/about/')}>About</div>
                 </li>
                 <li>
                   <div onClick={()=> history.push('/deploy/contact/')}>Contact</div>
                 </li>
            </ul>
        </nav>




    
    
           </div>
     </div>
    
    
    );
    
    export default withRouter(Header);




